@import "https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400&family=Montserrat:wght@200;400;600;800&display=swap";
html, body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
  overflow-x: hidden;
}

div#main {
  width: 100vw;
  flex-direction: column;
  display: flex;
}

section {
  width: 100vw;
  position: relative;
}

section.min-height {
  min-height: 100vh;
}

section.padding {
  padding: 5rem 0;
}

section img.background {
  height: 100%;
  object-fit: cover;
  pointer-events: none;
  width: 100vmax;
  z-index: -1;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%);
}

h1 {
  color: #dad9d9;
  margin: 0;
  font-family: Cormorant Garamond, serif;
  font-size: 7rem;
  font-weight: 400;
  line-height: .8;
}

h2 {
  color: #dad9d9;
  margin: 0;
  padding-top: 1rem;
  font-size: 2rem;
  font-weight: 400;
}

h3 {
  margin: 0;
  font-size: 1.25rem;
  font-weight: 400;
}

p {
  margin-bottom: 0;
  font-size: 18px;
  font-weight: 300;
  line-height: 1.5;
}

p.large {
  font-size: 24px;
}

p.small {
  font-size: 15px;
}

hr {
  margin: 2.5rem;
}

li {
  line-height: 1.7;
}

li::marker {
  color: #4e567e;
}

a {
  color: #000;
  text-decoration: none;
}

a:hover {
  color: #4e567e;
}

.container {
  grid-gap: 20px;
  grid-template-columns: 330px 300px;
  margin: 20px auto;
  display: grid;
}

.container .box {
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 10px;
  flex-direction: column;
  justify-content: flex-start;
  padding: 24px 16px;
  display: flex;
}

img.socialIcon {
  height: 30px;
  width: 30px;
}

.aboutul {
  text-align: left;
  columns: 2;
  column-gap: 3rem;
  margin: 2rem 3rem;
  font-size: 1.25rem;
}

.aboutmyself {
  width: 50%;
  text-align: center;
  background-color: #fff;
  margin: 3rem auto;
  padding: 4rem;
}

.portfolioimg {
  height: 90%;
  width: 100%;
  object-fit: cover;
}

.maintitle {
  color: #dad9d9;
  margin: 0;
  padding-top: 1rem;
  font-size: 2rem;
  font-weight: 400;
}

.headercss {
  width: 100%;
  z-index: 10;
  background: #ffffffbf;
  justify-content: center;
  gap: 2rem;
  padding: 1rem;
  display: flex;
  position: fixed;
  top: 0;
}

@media only screen and (max-width: 768px) {
  h1 {
    font-size: 5rem;
  }

  .container {
    grid-template-columns: 1fr;
  }

  .aboutul {
    column-gap: 3.5rem;
    margin: 1rem 1.5rem;
  }

  .aboutmyself {
    width: 60%;
  }

  .portfolioimg {
    height: 0;
    width: 0;
  }

  .maintitle {
    font-size: 1.4rem;
  }
}

@media only screen and (max-width: 480px) {
  h1 {
    margin-left: -25px;
    font-size: 3rem;
  }

  .maintitle {
    margin-left: -25px;
    font-size: 1.1rem;
  }

  .aboutul {
    column-gap: 4rem;
    margin: 0;
    font-size: 1.1rem;
  }

  .aboutmyself {
    width: 70%;
    text-align: center;
    margin: 3rem auto;
    padding: 2rem;
  }

  .portfolioimg {
    height: 0;
    width: 0;
  }

  .headercss {
    width: 90%;
    gap: 1.5rem;
  }
}

